<template>
    <div v-for="(a, i) in blog1" :key="i">
        <h5 @click="move(a.number)">{{ a.title }}</h5>
        <p>{{ a.date }}</p>
    </div>
</template>

<script>
export default {
    name : 'vueList',
    props : {
        blog1 : Object,
    },
    methods : {
        move(a) {
            return this.$router.push("/list/" + a);
        }
    }

}
</script>

